<!--  -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysOffice" ref="sysOfficeForm" :rules="rules" label-width="100px" class="edit-form">
      <el-form-item label="" class="hidden" prop="id">
        <sui-office v-model="sysOffice.id" :disabled="dialog.disabled"/>
      </el-form-item>
      <el-form-item v-if="sysOffice.pid!=1" label="" prop="pid">
        <sui-office v-model="sysOffice.pid" placeholder="" :disabled="dialog.disabled"/>
      </el-form-item>
      <el-form-item label="" prop="name">
        <el-input v-model="sysOffice.name" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-select v-model="sysOffice.type" placeholder="" :disabled="dialog.disabled" >
          <el-option v-for="dict in typeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="code">
        <el-input v-model="sysOffice.code" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
      <el-form-item label="" prop="remarks">
        <el-input type="textarea" v-model="sysOffice.remarks" placeholder="" :disabled="dialog.disabled"/>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel"> </el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('sysOfficeForm')"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import suiCascader from '@/components/sui-office/sui-office'
  export default {
    components: {
      suiCascader
    },
    name: "sysOfficeForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysOffice: {
          pid: "",
          name: "",
          code: "",
          type: "",
          sort: "",
          remarks: ""
        },
        //
        rules: {
          pid: [{required: true, message: '', trigger: 'blur'}],
          name: [{required: true, message: '', trigger: 'blur'}],
          type: [{required: true, message: '', trigger: 'change'}],
          code: [{required: true, message: '', trigger: 'blur'}],
        }
      }
    },
    computed: {
      //
      typeDict() {
        let dictArray = this.$store.getters.getDictArray('org_type');
        let orgType = dictArray.filter(function (v) {
          return (v.dictValue!=1)
        });
        return orgType
      },
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.sysOffice.pid=option.id;
        if (pageFrom == 'edit' || pageFrom == 'view') {
          //
          this.getSysOffice(option.id)
        }
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //id
      getSysOffice(id){
        this.$request.doGet("/api/sysOffice/get?id="+id).then(res => {
          if (res.isOk) {
            this.sysOffice = res.obj;
          }
        });
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/sysOffice/sub', that.sysOffice).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysOfficeForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>
</style>



