<!--  -->
<template>
  <div class="app-container">
    <el-row class="div-shadow" style="border: solid 1px rgba(128,128,128,0.41);height:700px;border-radius: 10px">
      <el-col :span="4">
        <office-tree ref="officeTree" :total="total" @treeSelected="treeSelected" style="padding: 10px"></office-tree>
      </el-col>
      <el-col :span="20" style="border-left: solid 1px rgba(128,128,128,0.41);height:700px;padding: 10px">
        <!--  -->
        <sysOffice-form ref="sysOfficeDialog" @reloadList="getList"/>
        <!--  -->
        <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
          <el-form-item label="" prop="name">
            <el-input v-model="queryForm.name" placeholder="" size="small"/>
          </el-form-item>
          <el-form-item label="" prop="type">
            <el-select v-model="queryForm.type" placeholder="" size="small" clearable>
              <el-option v-for="dict in typeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
            <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
          </el-form-item>
        </el-form>
        <!--  -->
        <el-row :gutter="10" class="mb8">
          <el-col :span="1.5">
            <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:''})" v-hasPermi="['sysOffice:add']"></el-button>
          </el-col>
          <el-col :span="1.5">
            <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['sysOffice:delete']"></el-button>
          </el-col>
          <right-toolbar @queryTable="getList"></right-toolbar>
        </el-row>
        <!--  -->
        <div style="height: 20px;margin: 10px;color: #808080">
          <span v-if="selectedName">{{selectedName}}</span>
        </div>
        <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
          <el-table-column type="selection" align="center" width="50"/>
          <el-table-column label="id"  prop="id" v-if="false"/>
          <el-table-column label="" type="index" width="50"/>
          <el-table-column label="" prop="name" sortable/>
          <el-table-column label="" prop="type" sortable>
            <template slot-scope="scope">
              <span v-for="dict in typeDict" v-if="dict.dictValue==scope.row.type">{{dict.dictLabel}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" prop="code" sortable/>
          <el-table-column label="" align="center"  width="380" fixed="right">
            <template slot-scope="scope">
              <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['sysOffice:view']"></el-button>
              <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['sysOffice:edit']"></el-button>
              <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'add',id:scope.row.id})" v-hasPermi="['sysOffice:edit']"></el-button>
              <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['sysOffice:delete']"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <div class="pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :background="true"
            :current-page="queryForm.current"
            :page-sizes="[5,10,20,30,50]"
            :page-size="queryForm.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>
      </el-col>
    </el-row>

  </div>
</template>

<script>
  import sysOfficeForm from './officeForm'
  import officeTree from './officeTree'
  export default {
    components: {sysOfficeForm,officeTree},
    data() {
      return {
        selectedName:"",
        loading: true,//
        selectedData: [],//
        total: 0, //
        dataList: [],
        queryForm: {
          current: 1,
          size: 10,
          pid: "1",
          name: "",
          code: "",
          type: "",
          sort: "",
          remarks: ""
        }
      };
    },
    mounted() {
      //
      this.getList();
    },
    computed: {
      //
      typeDict() {
        return this.$store.getters.getDictArray('org_type');
      },
    },
    methods: {
      //
      handleQuery() {
        this.queryForm.current = 1;
        this.getList();
      },
      treeSelected(data){
        this.queryForm.pid=data.id;
        this.selectedName = data.name;
        this.getList();
      },
      //
      getList() {
        this.$refs.officeTree.getOfficeTree();
        this.$request.doPost('/api/sysOffice/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        })
      },
      //
      resetQuery() {
        this.$refs['queryForm'].resetFields();
      },
      //
      openDialog(option) {
        this.$refs.sysOfficeDialog.open(option);
      },
      //
      handleSizeChange: function (size) {
        this.queryForm.size = size;
        this.getList();
      },
      //
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
      //
      handleSelectionChange(data) {
        this.selectedData = data;
      },
      //
      handleDeleteAll() {
        const that = this;
        that.showConfirm('?', function () {
          if (that.selectedData.length > 0) {
            that.selectedData.forEach(function (item, index) {
              that.delete(item.id);
            });
            that.showSuccess("")
          } else {
            that.showWarn("")
          }
        });
      },
      //
      handleDelete(id) {
        const that = this;
        that.showConfirm('?', function () {
          that.delete(id);
          that.showSuccess("");
        });
      },
      //
      delete(id) {
        this.$request.doGet('/api/sysOffice/delete?id=' + id).then(res => {
          if (res.isOk) {
            this.getList();
          };
        });
      },
    }
  };
</script>



