<!--  -->
<template>
  <div class="app-container">
    <!--  -->
    <sysUser-form ref="sysUserFormDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="" prop="loginName">
        <el-input v-model="queryForm.loginName" placeholder="" size="small"/>
      </el-form-item>
      <el-form-item label="" prop="roleId">
        <el-select v-model="queryForm.roleId" placeholder="" size="small" clearable>
          <el-option v-for="sysRole in sysRoleDict" :key="sysRole.id" :value="sysRole.id" :label="sysRole.roleName"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="officeId">
        <sui-office v-model="queryForm.officeId" placeholder="" size="small"/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({title:'',pageFrom:'add'})" v-hasPermi="['sysUser:add']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['sysUser:delete']"></el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id"  prop="id" v-if="false"/>
      <el-table-column label="" type="index" />
      <el-table-column label="" prop="loginName" sortable />
      <el-table-column label="" prop="roleId" sortable>
        <template slot-scope="scope">
          <span v-if="role" v-for="role in scope.row.roleList">{{role.roleName}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="officeId" sortable>
        <template slot-scope="scope">
          <span v-for="sysOffice in sysOfficeDict" v-if="sysOffice.id==scope.row.officeId">{{sysOffice.name}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="phone" sortable />
      <el-table-column label="" prop="email" sortable />
      <el-table-column label="" align="center"  width="500" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['sysUser:view']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['sysUser:edit']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="handleReset(scope.row)" v-hasPermi="['sysUser:edit']"></el-button>
          <el-button v-if="scope.row.disabled==1" size="mini" type="danger" plain icon="el-icon-edit" @click="handleUnLock(scope.row)" v-hasPermi="['sysUser:edit']"></el-button>
          <el-button v-else size="mini" type="primary" plain icon="el-icon-edit" @click="handleLock(scope.row)" v-hasPermi="['sysUser:edit']"></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row)" v-hasPermi="['sysUser:delete']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.pageNum"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import sysUserForm from './userForm'

  export default {
    components: {sysUserForm},
    data() {
      return {
        loading: true,//
        selectedData: [],//
        sysOfficeDict:[],
        sysRoleDict:[],
        total: 0, //
        dataList: [],
        queryForm: {
          current: 1,
          size: 10,
          photo: "",
          roleId:'',
          roleList:[],
          officeId:{},
          loginName: "",
          password: "",
          userName: "",
          userNo: "",
          email: "",
          phone: "",
          officeId: ""
        }
      };
    },
    mounted() {
      //
      this.getList();
      this.getSysOfficeDict();
      this.getSysRoleDict();
    },
    computed: {
    },
    methods: {
      //
      openDialog(option){
        this.$refs.sysUserFormDialog.open(option);
      },
      //
      handleQuery() {
        this.queryForm.current = 1;
        this.getList();
      },
      //
      getList() {
        this.$request.doPost('/api/sysUser/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        })
      },
      //
      getSysOfficeDict(){
        this.$request.doPost('/api/sysOffice/getList', {}).then(res => {
          if (res.isOk) {
            this.sysOfficeDict=res.list;
          }
        })
      },
      //
      getSysRoleDict(){
        this.$request.doPost('/api/sysRole/getList', {}).then(res => {
          if (res.isOk) {
            this.sysRoleDict=res.list;
          }
        })
      },
      //
      resetQuery() {
        this.$refs['queryForm'].resetFields();
      },
      //
      handleReset(row) {
        const that = this;
        that.showConfirm("",function () {
          that.$request.doGet('/api/sysUser/resetPassword?userId=' + row.id).then(res => {
            if (res.isOk) {
              that.getList();
              that.showSuccess(res.info);
            };
          });
        })
      },
      //
      handleLock(row) {
        const that = this;
        that.showConfirm("",function () {
          that.$request.doGet('/api/sysUser/lockUser?id=' + row.id+"&disabled=1").then(res => {
            if (res.isOk) {
              that.getList();
              that.showSuccess(res.info);
            };
          });
        })
      },
      //
      handleUnLock(row) {
        const that = this;
        that.showConfirm("",function () {
          that.$request.doGet('/api/sysUser/lockUser?id=' + row.id+"&disabled=0").then(res => {
            if (res.isOk) {
              that.getList();
              that.showSuccess(res.info);
            };
          });
        })
      },
      //
      handleSizeChange: function (size) {
        this.queryForm.size = size;
        this.getList();
      },
      //
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
      //
      handleSelectionChange(data) {
        this.selectedData = data;
      },
      //
      handleDeleteAll() {
        const that = this;
        that.showConfirm('?', function () {
          if (that.selectedData.length > 0) {
            that.selectedData.forEach(function (item, index) {
              that.delete(item.id);
            });
            that.showSuccess("")
          } else {
            that.showWarn("")
          }
        });
      },
      //
      handleDelete(row) {
        const that = this;
        that.showConfirm('?', function () {
          that.delete(row.id);
          that.showSuccess("");
        });
      },
      //
      delete(id) {
        this.$request.doGet('/api/sysUser/delete?id=' + id).then(res => {
          if (res.isOk) {
            this.getList();
          };
        });
      },
    }
  };
</script>



