<!--  -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysUser" ref="sysUserForm" :rules="rules" label-width="100px">
      <el-form-item label="id" class="hidden" prop="id">
        <el-input type="hidden" v-model="sysUser.id"/>
      </el-form-item>
      <el-form-item label="" prop="loginName">
        <el-input v-model="sysUser.loginName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="userName">
        <el-input v-model="sysUser.userName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="officeId">
        <sui-office v-model="sysUser.officeId" placeholder="" :disabled="dialog.disabled"/>
      </el-form-item>
      <el-form-item label="" prop="sex">
        <el-select v-model="sysUser.sex" placeholder="" :disabled="dialog.disabled" >
          <el-option v-for="dict in sexDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model="sysUser.phone" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="sysUser.email" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="roleList">
        <el-checkbox-group v-model="sysUser.roleList">
          <el-checkbox v-for="(item,index) in allRole" :disabled="dialog.disabled"  border size="medium" :key="index" :label="item" >{{item.roleName}}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel"> </el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('sysUserForm')"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  export default {
    components:{
    },
    name: "sysUserForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        allRole:[],
        //
        sysUser: {
          roleList:[],
          photo: "",
          loginName: "",
          password: "",
          userName: "",
          office:{},
          userNo: "",
          email: "",
          phone: "",
          officeId: ""
        },
        //
        rules: {
          photo: [{required: true, message: '', trigger: 'blur'}],
          loginName: [{required: true, message: '', trigger: 'blur'}],
          password: [{required: true, message: '', trigger: 'blur'}],
          userName: [{required: true, message: '', trigger: 'blur'}],
          sex: [{required: true, message: '', trigger: 'change'}],
          userNo: [{required: true, message: '', trigger: 'blur'}],
          email: [{type: 'email', message: '', trigger: ['blur', 'change']}],
          phone: [{required: false,validator:this.$validator.phone}],
          roleList: [{type: 'array', required: true, message: '', trigger: 'blur'}],
          officeId: [{required: true, message: '', trigger: ['blur', 'change']}]
        }
      }
    },
    computed: {
      sexDict() {
        return this.$store.getters.getDictArray('sys_sex');
      },
    },
    methods: {
      //
      open(option) {
        const that = this;
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        that.getRoleList();
        that.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          that.getUser(option.id);
        }
      },
      //
      getRoleList(userId){
        const that = this;
        that.$request.doGet('/api/sysUser/findCheckedRolesByUserId?userId='+userId).then(res => {
          if (res.isOk) {
            that.allRole = res.list;
            that.sysUser.roleList = that.allRole.filter(function (v) {
              return v.checked
            })
          }
        });
      },
      //
      getUser(id) {
        const that = this;
        this.$request.doGet("/api/sysUser/get?id="+id).then(res => {
          if (res.isOk) {
              that.sysUser = res.obj;
              that.getRoleList(id);
          }
        });
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/sysUser/sub', that.sysUser).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysUserForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
    }
  }
</script>

<style scoped>
</style>



