<!--  -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysLog" ref="sysLogForm" :rules="rules" label-width="100px" class="edit-form">
      <el-form-item label="" prop="loginName">
        <el-input v-model="sysLog.loginName" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
      <el-form-item label="ID" prop="userId">
        <el-input v-model="sysLog.userId" placeholder="ID" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
      <el-form-item label="" prop="systemType">
        <el-select v-model="sysLog.systemType" placeholder="" :disabled="dialog.disabled" >
          <el-option v-for="dict in systemTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="moduleName">
        <el-input v-model="sysLog.moduleName" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
      <el-form-item label="" prop="method">
        <el-input v-model="sysLog.method" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
      <el-form-item label="" prop="operationDate">
        <el-date-picker v-model="sysLog.operationDate" placeholder="" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd"/>
      </el-form-item>
      <el-form-item label="URI" prop="requestUri">
        <el-input v-model="sysLog.requestUri" placeholder="URI" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
      <el-form-item label="" prop="remotePort">
        <el-input v-model="sysLog.remotePort" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
      <el-form-item label="" prop="localName">
        <el-input v-model="sysLog.localName" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
      <el-form-item label="" prop="localAddr">
        <el-input v-model="sysLog.localAddr" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
      <el-form-item label="" prop="remoteHost">
        <el-input v-model="sysLog.remoteHost" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
      <el-form-item label="" prop="remoteAddr">
        <el-input v-model="sysLog.remoteAddr" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel"> </el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('sysLogForm')"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  export default {
    components: {

    },
    name: "sysLogForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysLog: {
          loginName: "",
          userId: "",
          systemType: "",
          moduleName: "",
          method: "",
          operationDate: "",
          requestUri: "",
          remotePort: "",
          localName: "",
          localAddr: "",
          remoteHost: "",
          remoteAddr: ""
        },
        //
        rules: {
          loginName: [{required: true, message: '', trigger: 'blur'}],
          userId: [{required: true, message: 'ID', trigger: 'blur'}],
          systemType: [{required: true, message: '', trigger: 'change'}],
          moduleName: [{required: true, message: '', trigger: 'blur'}],
          method: [{required: true, message: '', trigger: 'blur'}],
          operationDate: [{required: true, message: '', trigger: 'change'}],
          requestUri: [{required: true, message: 'URI', trigger: 'blur'}],
          remotePort: [{required: true, message: '', trigger: 'blur'}],
          localName: [{required: true, message: '', trigger: 'blur'}],
          localAddr: [{required: true, message: '', trigger: 'blur'}],
          remoteHost: [{required: true, message: '', trigger: 'blur'}],
          remoteAddr: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
      //
      systemTypeDict() {
        return this.$store.getters.getDictArray('system_type');
      },
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getSysLog(option.id)
        }
      },
      //id
      getSysLog(id) {
        this.$request.doGet("/api/sysLog/get?id="+id).then(res => {
          if (res.isOk) {
            this.sysLog = res.obj;
          }
        });
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/sysLog/sub', that.sysLog).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysLogForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>
</style>



