<!--  -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysMenu" ref="sysMenuForm" :rules="rules" label-width="100px" class="edit-form">
      <el-form-item label="" prop="name">
        <el-input v-model="sysMenu.name" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
      <el-form-item label="" prop="component">
        <el-input v-model="sysMenu.component" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
<!--      <el-form-item label="" prop="isShow">-->
<!--        <el-select v-model="sysMenu.isShow" placeholder="" :disabled="dialog.disabled" >-->
<!--          <el-option v-for="dict in isShowDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>-->
<!--        </el-select>-->
<!--      </el-form-item>-->
      <el-form-item label="" prop="href">
        <el-input v-model="sysMenu.href" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
      <el-form-item label="" prop="permission">
        <el-input v-model="sysMenu.permission" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
      <el-form-item label="" prop="sort">
        <el-input v-model="sysMenu.sort" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
      <el-form-item label="" prop="pid">
        <el-input v-model="sysMenu.pid" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
      <el-form-item label="" prop="icon">
        <el-input v-model="sysMenu.icon" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
      </el-form-item>
      <el-form-item label="" prop="remarks">
        <el-input type="textarea" v-model="sysMenu.remarks" placeholder="" :disabled="dialog.disabled"/>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel"> </el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('sysMenuForm')"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  export default {
    components: {

    },
    name: "sysMenuForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysMenu: {
          name: "",
          component: "",
          href: "",
          permission: "",
          sort: "",
          pid: "",
          icon: "",
          remarks: ""
        },
        //
        rules: {
          name: [{required: true, message: '', trigger: 'blur'}],
          isShow: [{required: true, message: '', trigger: 'change'}],
          sort: [{required: true, message: '', trigger: 'blur'}],
          pid: [{required: true, message: '', trigger: 'blur'}],
        }
      }
    },
    computed: {
      //
      isShowDict() {
        return this.$store.getters.getDictArray('sys_yes_no');
      },
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getSysMenu(option.id)
        }
      },
      //id
      getSysMenu(id) {
        this.$request.doGet("/api/sysMenu/get?id="+id).then(res => {
          if (res.isOk) {
            this.sysMenu = res.obj;
          }
        });
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/sysMenu/sub', that.sysMenu).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysMenuForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>
</style>



