<!--  -->
<template>
  <div class="app-container">
    <!--  -->
    <sysLog-form ref="sysLogDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="" prop="loginName">
        <el-input v-model="queryForm.loginName" placeholder="" size="small"/>
      </el-form-item>
<!--      <el-form-item label="" prop="systemType">-->
<!--        <el-select v-model="queryForm.systemType" placeholder="" size="small" clearable>-->
<!--          <el-option v-for="dict in systemTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>-->
<!--        </el-select>-->
<!--      </el-form-item>-->
      <el-form-item label="" prop="moduleName">
        <el-input v-model="queryForm.moduleName" placeholder="" size="small"/>
      </el-form-item>
      <el-form-item label="" prop="operationDate">
        <el-date-picker v-model="queryForm.operationDate" placeholder="" type="date" value-format="yyyy-MM-dd" size="small" clearable/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:''})" v-hasPermi="['sysLog:add']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['sysLog:delete']"></el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id"  prop="id" v-if="false"/>
      <el-table-column label="" type="index" width="50"/>
      <el-table-column label="" prop="loginName" sortable/>
      <el-table-column label="ID" prop="userId" sortable/>
      <el-table-column label="" prop="systemType" sortable>
        <template slot-scope="scope">
          <span v-for="dict in systemTypeDict" v-if="dict.dictValue==scope.row.systemType">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="moduleName" sortable/>
      <el-table-column label="" prop="method" sortable/>
      <el-table-column label="URI" prop="requestUri" sortable/>
      <el-table-column label="" align="center"  width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['sysLog:view']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['sysLog:edit']"></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['sysLog:delete']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import sysLogForm from './logForm'
  export default {
    components: {sysLogForm},
    data() {
      return {
        loading: true,//
        selectedData: [],//
        total: 0, //
        dataList: [],
        queryForm: {
          current: 1,
          size: 10,
          loginName: "",
          userId: "",
          systemType: "",
          moduleName: "",
          method: "",
          operationDate: "",
          requestUri: "",
          remotePort: "",
          localName: "",
          localAddr: "",
          remoteHost: "",
          remoteAddr: ""
        }
      };
    },
    mounted() {
      //
      this.getList();
    },
    computed: {
      //
      systemTypeDict() {
        return this.$store.getters.getDictArray('system_type');
      },
    },
    methods: {
      //
      handleQuery() {
        this.queryForm.current = 1;
        this.getList();
      },
      //
      getList() {
        this.$request.doPost('/api/sysLog/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        })
      },
      //
      resetQuery() {
        this.$refs['queryForm'].resetFields();
      },
      //
      openDialog(option) {
        this.$refs.sysLogDialog.open(option);
      },
      //
      handleSizeChange: function (size) {
        this.queryForm.size = size;
        this.getList();
      },
      //
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
      //
      handleSelectionChange(data) {
        this.selectedData = data;
      },
      //
      handleDeleteAll() {
        const that = this;
        that.showConfirm('?', function () {
          if (that.selectedData.length > 0) {
            that.selectedData.forEach(function (item, index) {
              that.delete(item.id);
            });
            that.showSuccess("")
          } else {
            that.showWarn("")
          }
        });
      },
      //
      handleDelete(id) {
        const that = this;
        that.showConfirm('?', function () {
          that.delete(id);
          that.showSuccess("");
        });
      },
      //
      delete(id) {
        this.$request.doGet('/api/sysLog/delete?id=' + id).then(res => {
          if (res.isOk) {
            this.getList();
          };
        });
      },
    }
  };
</script>



